import axios from 'axios'
export default function ajax(url, data={}, type='GET') {
  return new Promise((resolve, reject) => {
    let promise
    // 
    if(type==='GET') {
      promise = axios.get(url, { // 
        params: data // query
      })
    } else {
      promise = axios.post(url, data)
    }

    promise.then(response => {
      // , resolve
      resolve(response.data)
    }).catch(error => {
      // , reject
      // reject(error)
      // 
      alert(': '+error.message)
    })
  })
}



